.container-login{
    display:flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5rem;
}

.container-login a{
    font-size: 3rem;
}

.container-login p{
    font-size: 2rem;
}
.container{
    width:100%;
    padding:0!important;
    margin:0!important;
    max-width: 100%!important;
    background: lightblue;
}

.fa{
    cursor: pointer;
}

.container img{
    display: block;
    margin: 0 auto;
}

.wrapper-top-header h1{
    padding-bottom: 2rem;
    font-size: 3rem;
}

.container audio{
    width: 100%;
    display:block;
    margin: 0 auto;
}

.container-current-top{
    padding-top: 2rem;
    padding-bottom: 5rem;
    display:flex;
    justify-content: center;
}

.wrapper-top-preview{
    width: 30rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container-favorite-genres{
    padding-bottom: 4rem;
    width:100%;
}

.progress{
    width: 25%;
    margin-top: 0.5rem;
}

.genres{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
}


.fontawesome{
    width: 100px;
    height:100px;
    display:block;
}
body{
    overflow-x: hidden;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

